<template>
  <div class="mini-mnist">
    <canvas
      id="mnist-draw"
      ref="mnistDraw"
      width="280"
      height="280"
      @mousedown="handleDrawMouseDown"
      @mousemove="handleDrawMouseMove"
      @mouseup="handleDrawMouseUp"
      @mouseout="handleDrawMouseUp"
    />
    <canvas
      id="mnist-hidden"
      ref="mnistHidden"
      width="28"
      height="28"
    />
    <div>
      <button @click="handleClear">Clear</button>
    </div>
    <div>
      {{ answer }}
    </div>
  </div>
</template>

<script>
import * as tf from '@tensorflow/tfjs';

export default {
  name: 'MiniMnist',
  data() {
    return {
      model: null,
      drawing: false,
      lastMousePos: {
        x: 0,
        y: 0,
      },
      answer: null,
    };
  },

  methods: {
    handleDrawMouseDown(event) {
      this.drawing = true;
      this.lastMousePos.x = event.offsetX;
      this.lastMousePos.y = event.offsetY;

      const ctx = this.$refs.mnistDraw.getContext("2d");
      ctx.lineWidth = 15;
      ctx.lineCap="round";
      ctx.lineJoin = "round";
      ctx.beginPath();
    },

    handleDrawMouseMove(event) {
      if (!this.drawing) {
        return;
      }

      const ctx = this.$refs.mnistDraw.getContext("2d");
      ctx.moveTo(this.lastMousePos.x, this.lastMousePos.y);
      ctx.lineTo(event.offsetX, event.offsetY);
      ctx.stroke();

      this.lastMousePos.x = event.offsetX;
      this.lastMousePos.y = event.offsetY;
    },
      // computes center of mass of digit, for centering
      // note 1 stands for black (0 white) so we have to invert.
      centerImage(img) {
        var meanX = 0;
        var meanY = 0;
        var rows = img.length;
        var columns = img[0].length;
        var sumPixels = 0;
        for (var y = 0; y < rows; y++) {
          for (var x = 0; x < columns; x++) {
            var pixel = (1 - img[y][x]);
            sumPixels += pixel;
            meanY += y * pixel;
            meanX += x * pixel;
          }
        }
        meanX /= sumPixels;
        meanY /= sumPixels;
        
        var dY = Math.round(rows/2 - meanY);
        var dX = Math.round(columns/2 - meanX);
        return {transX: dX, transY: dY};
      },

      // given grayscale image, find bounding rectangle of digit defined
      // by above-threshold surrounding
      getBoundingRectangle(img, threshold) {
        var rows = img.length;
        var columns = img[0].length;
        var minX=columns;
        var minY=rows;
        var maxX=-1;
        var maxY=-1;
        for (var y = 0; y < rows; y++) {
          for (var x = 0; x < columns; x++) {
            if (img[y][x] < threshold) {
              if (minX > x) minX = x;
              if (maxX < x) maxX = x;
              if (minY > y) minY = y;
              if (maxY < y) maxY = y;
            }
          }
        }
        return { minY: minY, minX: minX, maxY: maxY, maxX: maxX};
      },

      // take canvas image and convert to grayscale. Mainly because my
      // own functions operate easier on grayscale, but some stuff like
      // resizing and translating is better done with the canvas functions
      imageDataToGrayscale(imgData) {
        var grayscaleImg = [];
        for (var y = 0; y < imgData.height; y++) {
          grayscaleImg[y]=[];
          for (var x = 0; x < imgData.width; x++) {
            var offset = y * 4 * imgData.width + 4 * x;
            var alpha = imgData.data[offset+3];
            // weird: when painting with stroke, alpha == 0 means white;
            // alpha > 0 is a grayscale value; in that case I simply take the R value
            if (alpha == 0) {
              imgData.data[offset] = 255;
              imgData.data[offset+1] = 255;
              imgData.data[offset+2] = 255;
            }
            imgData.data[offset+3] = 255;
            // simply take red channel value. Not correct, but works for
            // black or white images.
            grayscaleImg[y][x] = imgData.data[y*4*imgData.width + x*4 + 0] / 255;
          }
        }
        return grayscaleImg;
      },

    handleDrawMouseUp() {
      if (!this.drawing) {
        return;
      }

      this.drawing = false;

      const ctx = this.$refs.mnistDraw.getContext("2d");
      // convert RGBA image to a grayscale array, then compute bounding rectangle and center of mass  
      var imgData = ctx.getImageData(0, 0, 280, 280);
      var grayscaleImg = this.imageDataToGrayscale(imgData);
      var boundingRectangle = this.getBoundingRectangle(grayscaleImg, 0.01);
      var trans = this.centerImage(grayscaleImg); // [dX, dY] to center of mass
      
      // copy image to hidden canvas, translate to center-of-mass, then
      // scale to fit into a 200x200 box (see MNIST calibration notes on
      // Yann LeCun's website)
      var canvasCopy = document.createElement("canvas");
      canvasCopy.width = imgData.width;
      canvasCopy.height = imgData.height;
      var copyCtx = canvasCopy.getContext("2d");
      var brW = boundingRectangle.maxX + 1 - boundingRectangle.minX;
      var brH = boundingRectangle.maxY + 1 - boundingRectangle.minY;
      var scaling = 190 / Math.max(brW, brH);
      // scale
      copyCtx.translate(this.$refs.mnistDraw.width / 2, this.$refs.mnistDraw.height / 2);
      copyCtx.scale(scaling, scaling);
      copyCtx.translate(-this.$refs.mnistDraw.width / 2, -this.$refs.mnistDraw.height / 2);
      // translate to center of mass
      copyCtx.translate(trans.transX, trans.transY);
      copyCtx.drawImage(ctx.canvas, 0, 0);
      
      const hiddenCtx = this.$refs.mnistHidden.getContext("2d");
      hiddenCtx.clearRect(0, 0, 28, 28);
      hiddenCtx.drawImage(canvasCopy, 0, 0, 28, 28);

      this.makePrediction();
    },

    handleClear() {
      const ctx = this.$refs.mnistDraw.getContext("2d");
      ctx.clearRect(0, 0, 300, 300);
      this.answer = null;
    },

    async makePrediction() {
      const imgData = tf.div(tf.fromPixels(this.$refs.mnistHidden, 4).asType('float32'), 255.);
      const alphaChannel = imgData.slice([0, 0, 3], [28, 28, 1]).expandDims();
      const prediction = this.model.predict(alphaChannel);
      const answer = tf.argMax(prediction.squeeze());
      const answerData = await answer.data();
      [this.answer] = answerData;
    },
  },

  async created() {
    this.model = await tf.loadModel('models/mnist/model.json');
  }
}
</script>

<style>
#mnist-draw {
  border: 1px solid black;
}

#mnist-hidden {
  display: none;
}
</style>
